import React from 'react'

// material-ui
import { Toolbar, Box, Fab, Divider } from '@material-ui/core'
import KeyboardArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'

// custom component
import ArticlesTagsToolbar from '../components/Toolbars/ArticlesTagsToolbar'
import SiteHeader from '../components/Headers/SiteHeader'
import FluidContainerList from '../components/Lists/FluidContainerList'
import ScrollTop from '../components/ScrollTop/ScrollTop'
import Footer from '../components/Footer/Footer'

// articles data query
import useArticles from '../data/useArticles'
import useSiteMetaData from '../data/useSiteMetaData'

/**
 * Articles page
 * @param location
 * @param navigate
 * @return {JSX.Element}
 * @constructor
 */
function Articles({ location, navigate }) {
  /**
   * Load all data for the page and pass it as props
   */
  const siteMeta = useSiteMetaData()
  const articles = useArticles()

  return (
    <React.Fragment>
      <SiteHeader title="Articles" siteMeta={siteMeta} location={location} navigate={navigate} />
      {/* target for scrollTop component */}
      <Toolbar id="back-to-top-anchor" />
      <Box pt={8} pr={1} pb={2} pl={1}>
        <ArticlesTagsToolbar />
        <Box pt={1} pb={1}>
          <Divider variant="fullWidth" />
        </Box>
        <FluidContainerList edges={articles} location={location} />
      </Box>
      <Footer siteMeta={siteMeta} />
      <ScrollTop>
        <Fab color="secondary" aria-label="scroll back to top">
          <KeyboardArrowUpIcon />
        </Fab>
      </ScrollTop>
    </React.Fragment>
  )
}

export default Articles
